import { forwardRef } from "react";

const Button = forwardRef(({ style, className, children, ...rest }, ref) => {
  const baseStyle = {
    border: "4px solid black",
    background: "white",
    paddingLeft: "10px",
    paddingRight: "10px",
    paddingTop: "2px",
    paddingBottom: "2px",
  };

  const finalStyle = { ...baseStyle, ...style };

  return (
    <button ref={ref} {...rest} style={finalStyle} className={className}>
      {children}
    </button>
  );
});

export default Button;
