function OptionItem({ selected, onClick, item, specialStyleByItem }) {
  let style = {
    border: "0px",
    listStyleType: "none",
    listStylePosition: "inside",
    textAlign: "center",
    background: "white",
    color: "black",
    height: "20px",
  };

  if (specialStyleByItem) {
    style = { ...style, ...specialStyleByItem(item) };
  }

  if (selected === item) {
    style.background = "#00ccff ";
  }

  const handleClick = () => {
    onClick(item);
  };

  return (
    <li onClick={handleClick} style={style}>
      {item}
    </li>
  );
}

export default OptionItem;
